  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
/* Header Navigasi */
.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    background-color: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.skip-link {
  position: absolute;
  top: -50px;
  left: 0;
  background-color: #000;
  color: #fff;
  padding: 8px;
  z-index: 100;
  text-decoration: none;
}

.skip-link:focus {
  top: 0;
}


/* Header Navbar */
.navbar {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}

nav ul {
    list-style-type: none;
    display: grid;
    grid-template-columns: 70px 80px 120px;
    justify-content: right;
}

nav ul li a {
    padding: 15px;
    text-decoration: none;
    color: #000;
}

nav ul li a:hover {
    color: goldenrod;
}

/*hamburger*/
.hamburger {
  display: none;
  z-index: 99;
  position: fixed;
  top: 16px;
  right: 35px;
  min-width: 44px;
  min-height: 44px;
}

.hamburger div {
  width: 25px;
  height: 2px;
  background-color: #000;
  margin: 7px;
  transition: all 0.3s;
  z-index: 99;
}


.logo img {
  margin-left: 15%;
}

.headline_content {
  text-align: center;
}

/* Style the links when hovered */
.menu a:hover {
  text-decoration: underline;
}

/*Hero */
.hero {
    display: flex;
    align-items: center;
    min-height: 550px;
    width: 100%;
    text-align: center;
    /*background-image: url("/dist/images/heros/hero-image_3-large.jpg");*/
    background-position: center;
    background-repeat: repeat;
    background-color: green;
}
/*pada img ini, karena beda dengan latihan, saya lakukan
penyesuaian dengan melakukan sharing, baik dengan forum, dan juga bebarapa orang
jadilah dengan menggunakan ini*/
@media only screen and (max-width: 650px) {

    .app-main__hero {

        background:url(${___CSS_LOADER_URL_REPLACEMENT_0___});

    }

}

@media only screen and (min-width: 651px) {

    .app-main__hero {

        background:url(${___CSS_LOADER_URL_REPLACEMENT_1___});

    }

}
.hero_inner {
  margin: 0 auto;
}
.hero_title {
  color: forestgreen;
  font-weight: 700;
  text-decoration: solid;
  text-shadow: #000;
  font-style: inherit;
  font-size: 40px;
}
.content {
  padding: 32px;
}
.hero_line {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  text-shadow: 2px 2px 4px rgba(26, 23, 23, 0.76);
}

/* restoran */
.movies {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  margin: 32px 0;
}
 
 
/*
  Movie Item
*/
 
.movie-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
}
 
.movie-item__header {
  position: relative;
}
 
.movie-item .movie-item__header #movie-item__header__poster {
  width: 100%;
}
 
.movie-item .movie-item__header .movie-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  /* background-color: black; */
  color: white;
}
 
.movie-item .movie-item__header .movie-item__header__rating .movie-item__header__rating__score {
  margin-left: 10px;
}
 
.movie-item .movie-item__content {
  padding: 16px;
}
 
.movie-item .movie-item__content h3 {
  margin: 0 0 10px 0;
}
 
.movie-item .movie-item__content h3 a {
  color: #db0000;
  text-decoration: none;
}
 
.movie-item .movie-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}
 
 
/*
  Movie
*/
 
.movie {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}
 
.movie .movie__poster {
  width: 100%;
  max-width: 400px;
}
 
.movie .movie__info h4 {
  margin: 8px 0;
}

footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  /* padding: 10px; */
  bottom: 0;
  width: 100%;
  padding: 1rem 0;
}
#restaurantList {
  margin: 32px auto auto;
  text-align: left;
  justify-content: center;
}
@media screen and (min-width: 550px) {

  /*    card*/
  #restaurantList {
      display: grid;
      grid-template-columns: repeat(2, 50%);
      gap: 20px;
  }

}

@media screen and (max-width: 680px) {
  .hamburger {
      display: inline-block;
      cursor: pointer;
  }

  .toggle-burger .line1 {
      transform: rotate(-45deg) translate(-5px, 7px);
  }

  .toggle-burger .line2 {
      opacity: 0;
  }

  .toggle-burger .line3 {
      transform: rotate(45deg) translate(-5px, -7px);
  }

  /*    nav*/
  nav {
      position: fixed;
      width: 100%;
      right: 0;
      top: -100%;
      height: 100vh;
      flex-direction: column;
      background: #fff;
      border-left: 1px solid #dfdcdc;
      z-index: 9;
      transition: .4s ease;
  }

  nav ul {
      display: flex;
      height: 100vh;
      align-items: center;
      flex-direction: column;
      justify-content: center;
  }

  nav ul li a::after {
      content: '';
      display: block;
      padding-bottom: 0.5rem;
      border-bottom: .1rem solid #5b8273;
      transform: scaleX(0);
      transition: 0.2s;
  }

  nav ul li a:hover {
      box-shadow: none;
      background-color: transparent;
      color: #5b8273;
  }

  nav ul li a:hover::after {
      transform: scaleX(.5);
  }

  .nav-active {
      top: 0;
  }
}


@media screen and (min-width: 900px) {

  #restaurantList {
      display: grid;
      grid-template-columns: repeat(3, 30%);
      gap: 29px;
  }
